import React from 'react';
import { format, parseISO } from 'date-fns';
// Services 
import utilityService from '../../../../services/utils/utilityServices';
// Redux
import { useSelector, useDispatch } from 'react-redux';
import { getComakers } from '../../../../redux/async/comakerAsync';
// MUI Components
import { DataGrid, GridActionsCellItem } from '@mui/x-data-grid';
import { Typography } from '@mui/material';
// Page Components
import GridLoadingOverlay from '../../grid/GridLoadingOverlay';
import GridNoRowOverlay from '../../grid/GridNoRowOverlay';
import Toolbar from '../../grid/Toolbar';
// React icons
import { FaLightbulb, FaSlidersH, FaEye, FaHandshake, FaHandshakeAltSlash } from 'react-icons/fa';
import toastService from '../../../../services/toast/toastService';
import alertService from '../../../../services/alert/alertService';
import comakerRepository from '../../../../repositories/comakerRepository';

const LoadingOverlayOverride = () => {
    return (
        <GridLoadingOverlay />
    );
}

const NoRowOverlayOverride = () => {
    return (
        <GridNoRowOverlay />
    );
}

const ToolbarOverride = (props) => {
    return (
        <Toolbar
            showCreateAccount={true}
            createAccount={props.createAccount}
            showExport={false}
        />
    );
}

const ActionsIcon = () => {
    return <FaSlidersH />
}


export default function MyComakersDataTable(props) {

    const dispatch = useDispatch();
    const comakersRedux = useSelector((state) => state.comakers.value);
    const authRedux = useSelector((state) => state.auth.value);
    const uuid = authRedux?.me?.uuid;
    const loading = comakersRedux.loading;
    const [rowData, setRowData] = React.useState([]);

    const [memberAccount, setMemberAccount] = React.useState(null);

    const [accountDetailsModal, setAccountDetailsModal] = React.useState(false);
    const toggleAccountDetailsModal = () => setAccountDetailsModal(!accountDetailsModal);
    const [newAccountModal, setNewAccountModal] = React.useState(false);
    const toggleNewAccountModal = () => setNewAccountModal(!newAccountModal);

    const width = 300;

    const columns = [
        {
            field: 'loan_code', headerName: 'Loan Code', width: width
        },
        {
            field: 'member', headerName: 'Member', width: width
        },
        {
            field: 'date_requested', headerName: 'Date Requested', flex: true, renderCell: (params) => {
                return (
                    <div>
                        <span>
                            {params.row.date_requested === null ? "..." : format(parseISO(params.row.date_requested), "MMMM dd, yyyy")}
                        </span>
                    </div>
                );
            }
        },
        {
            field: 'status', headerName: 'Status', headerAlign: 'center', align: 'center', width: 140, renderCell: (params) => {
                let status = "No Actions";
                let color = "#ccc";

                if (params.row.status === -1) {
                    status = "Declined";
                    color = "#E45570";
                } else if (params.row.status === 1) {
                    status = "Accepted";
                    color = "#00BFA5";
                }

                return (

                    <div style={{
                        borderWidth: 2,
                        borderStyle: 'solid',
                        borderRadius: 7,
                        borderColor: color,
                        backgroundColor: color,
                        color: "#FFFFFF",
                        height: 25,
                        width: '70%',
                        textAlign: 'center',
                        display: 'flex',
                        justifyContent: 'center',
                        alignItems: 'center',
                    }}>
                        {status}
                    </div>
                );
            },
        },
        {
            field: 'actions', headerName: 'Options', width: width, type: 'actions', getActions: (params) => {
                return [
                    <GridActionsCellItem
                        icon={<FaHandshake size={18} color="#00BFA5" />}
                        onClick={() => handleAcceptRequest(params.row)}
                        label="Accept Request"
                        sx={{
                            '&:hover': {
                                color: "#00BFA5"
                            }
                        }}
                        showInMenu
                    />,
                    <GridActionsCellItem
                        color='secondary'
                        icon={<FaHandshakeAltSlash size={18} color="#E45570" />}
                        onClick={() => handleDeclineRequest(params.row)}
                        label="Decline Request"
                        sx={{
                            '&:hover': {
                                color: "#E45570"
                            }
                        }}
                        showInMenu
                    />,
                ]
            }
        },
    ];

    const handleAcceptRequest = async (data) => {

        try {
            alertService.process("Accepting Request...");

            let params = {
                loan_code: data?.loan_code,
                status: 1,
                uuid: data?.data?.uuid
            }

            let result = await comakerRepository.changeStatus(params);

            if (!result.error) {
                toastService.success(result.message);
            } else {
                toastService.error(result.message);
            }

            loadMyComakers();
        } catch (e) {
            toastService.error(e);
        }

    }

    const handleDeclineRequest = async (data) => {

        try {
            alertService.process("Declining Request...");

            let params = {
                loan_code: data?.loan_code,
                status: -1,
                uuid: data?.data?.uuid
            }

            let result = await comakerRepository.changeStatus(params);

            if (!result.error) {
                toastService.success(result.message);
            } else {
                toastService.error(result.message);
            }

            loadMyComakers();
        } catch (e) {
            toastService.error(e);
        }

    }

    const loadMyComakers = React.useCallback(() => {
        dispatch(getComakers());
    }, [dispatch, uuid])

    React.useEffect(() => {
        let mounted = false;

        if (!mounted) {
            loadMyComakers();
        }

        return () => mounted = true;
    }, [loadMyComakers]);

    return (
        <div style={{ height: 500, width: '100%', padding: 10 }}>
            <Typography variant="subtitle2" sx={{ mb: 1 }}>
                <FaLightbulb size={14} style={{ marginRight: 2, color: "#00BFA5" }} />
                Note: By clicking accept with enable you as co-maker of a loan
            </Typography>
            <DataGrid
                rows={comakersRedux?.myComakers}
                columns={columns}
                onRowDoubleClick={(params) => handleAcceptRequest(params.row)}
                loading={loading}
                density="standard"
                disableSelectionOnClick
                disableColumnMenu
                components={{
                    LoadingOverlay: LoadingOverlayOverride,
                    NoRowsOverlay: NoRowOverlayOverride,
                    Toolbar: ToolbarOverride,
                    MoreActionsIcon: ActionsIcon
                }}
                componentsProps={{
                    toolbar: {
                        createAccount: () => toggleNewAccountModal()
                    },
                }}
            />
        </div>
    );
}